
/* Общие стили */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background: linear-gradient(to bottom, #9A00AE, #1D5CCE);
    color: white;
    line-height: 1.6;
}


.app-container {
  /* max-width: 800px; */
  margin: 0 auto;
  min-height: 100vh;
}

/* Заголовки */
h2 {
  text-align: center;
  margin: 10px 0;
      color: white;
}

/* Кнопки — общие стили */
button {
  padding: 12px 24px;
  border: none;
  border-radius: 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* FirstScreen (если есть) */
.first-screen {
  text-align: center;
  padding: 40px 20px;
}

/* ModeScreen */
.mode-screen {
  text-align: center;
}

.modes-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin: 30px 0;
}

.mode-btn {
  background-color: #3498db;
  color: white;
}

.mode-btn:hover {
  background-color: #2980b9;
  transform: translateY(-1px);
}

/* PlayersScreen */
.players-screen {
  padding: 30px;
}

.player-input-group {
  display: flex;
  gap: 20px;
  margin-bottom: 30px;
}

.player-input {
  flex-grow: 1;
  padding: 14px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
  outline: none;
}

.player-input:focus {
  border-color: #3498db;
}

.btn-add {
  color: black;
  font-weight: 600;
}

.btn-add:hover {
  background-color: #7f8c8d;
  transform: translateY(-1px);
}

.players-list {
  list-style: none;
  padding: 0;
  margin: 30px 0;
  max-height: 60vh;
  overflow-y: auto;
  border-radius: 8px;
}

.player-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 20px;
  border-bottom:  2px solid #eee;
  margin: 10px 0;
}

.player-item:last-child {
  border-bottom: none;
}

.player-name {
  font-size: 20px;
  color: white;
  font-weight: 600;
  flex-grow: 1;
  margin-right: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.btn-remove {
  background-color: #e74c3c;
  color: white;
  border: none;
  border-radius: 30px;
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.btn-remove:hover {
  background-color: #c0392b;
}

.navigation-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.btn-back {
  background-color: white;
  color: black;
  font-weight: 600;
}

.btn-back:hover {
  background-color: #7f8c8d;
  transform: translateY(-1px);
}



.btn-next {
  background-color: white;
  color: black;
  font-weight: 600;
}

.btn-next:hover {
  background-color: #7f8c8d;
  transform: translateY(-1px);
}





/* QuestionScreen */
.question-screen {
  position: relative;
  padding: 30px;
  height: 100vh;
  text-align: center;
}

.btn-exit {
  position: absolute;
  top: 15px;
  left: 15px;
  background-color: white;
  color: black;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

.btn-exit:hover {
  background-color: #7f8c8d;
  transform: translateY(-1px);
}

.question-content {
  user-select: none;
  margin: 30vh 0 0 0;
  font-size: 20px;
  line-height: 1.8;
  color: white;
  font-weight: 600;
}

.question-content strong {
  font-weight: bold;
  color: black;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 2px 6px;
  border-radius: 4px;
}

/* Кнопка озвучки */
.btn-speak {
  position: absolute;
  top: 15px;
  right: 15px;
  background-color: white;
  color: white;
  border: none;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 18px;
  transition: background-color 0.3s, transform 0.2s;
}

.btn-speak:hover {
  background-color: white;
  transform: scale(1.1);
}

.btn-speak:active {
  transform: scale(0.95);
}

.btn-speak.speaking {
  background-color: red;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

/* Блок с наказанием */
.punishment-block {
  margin-top: 60px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  padding: 20px 0;
}

.punishment-block h4 {
  margin: 0 0 8px 0;
  color: white;
  font-size: 20px;
  font-weight: 600;
}




.mode-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  gap: 15px; /* расстояние между карточками */
}

.title {
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: var(--white);
}

.mode-card {
  text-align: left;
  display: flex;
  align-items: center;
  padding: 20px;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 500px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.mode-card:hover {
  transform: translateY(-3px);
}

.mode-image {
  width: 80px;
  height: 80px;
  margin-right: 15px;
  border-radius: 8px;
}

.mode-content {
  flex: 1;
}

.mode-title {
  text-align: left;
  margin: 0 0 5px 0;
  font-size: 1.4rem;
  font-weight: 600;
}

.mode-description {
  margin: 0;
  font-size: 0.8rem;
  line-height: 1.4;
}

.arrow-icon {
  font-size: 2rem;
  margin-left: 49px;
  margin-top: -5px;
}


.next-button-container
{
  position: absolute;
  top: 20vh;
  left: 0px;
  width: 100vw;
  height: 80vh;
}

.next-button-container h2
{
  user-select: none;
  margin-top: 60vh;
  bottom: 10vh;
  color: rgba(255, 255, 255, 0.9);
}


.first-screen
{
  height: 40vh;
  width: 100vw;
  position: absolute;
  top: 20vh;
}


